import React, { useRef, useState } from 'react'
import Modal from 'antd/es/modal'

import Button from 'antd/es/button'
import Input from 'antd/es/input'
import Form from 'antd/es/form'
import Upload from 'antd/es/upload'
import PlusOutlined from '@ant-design/icons/PlusOutlined'

import { uploadFile } from '../../utils/uploadFile'
import Rewards from '../Rewards/Rewards'
import { nameRules, descriptionRules } from '../../constants/fieldRules'
import connectionsSelect from './connectionsSelect'
import Select from 'antd/es/select'
import { Map, TileLayer } from 'react-leaflet'
import 'leaflet/dist/leaflet.css'
import Checkbox from 'antd/es/checkbox'
import MapSearch from '../UI/MapSearch'
import LocateControl from '../../containers/Explore/LocateControl'

const { Option } = Select

const EditGoalAndProgressForm = props => {
    const map = useRef(null)

    const { goal, account, friendData } = props

    const [images, setImages] = useState(
        (goal &&
            goal.images.map((url, index) => ({
                url,
                uid: index,
            }))) ||
            []
    )

    const [previewImage, setPreviewImage] = useState('')
    const defPos = [40.689078, -74.044526]
    const [position, setPosition] = useState(
        goal && goal.position && goal.position.coordinates
            ? [goal.position.coordinates[1], goal.position.coordinates[0]]
            : defPos
    )

    const [zoom, setZoom] = useState(10)

    const [rewards, setRewards] = useState((goal && goal.rewards) || [])

    const onFinish = value => {
        props.onFinish({
            ...value,
            images: images.map(image => image.url),
            rewards,
            position: {
                type: 'Point',
                coordinates: [position[1], position[0]],
            },
        })
    }

    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    }

    const [form] = Form.useForm()

    const normFile = e => {
        if (Array.isArray(e)) {
            return e
        }
        return e && e.fileList.map(image => image.url)
    }

    const connections = connectionsSelect(account, friendData)

    const mapDragEnd = (e, fromZoom) => {
        setZoom(e.target._zoom)

        if (!fromZoom) setPosition(e.target.getCenter())
        form.setFieldsValue({ nomap: false })
        if (fromZoom && map.current && map.current.leafletElement)
            map.current.leafletElement.panTo(position, {
                animate: false,
            })
    }

    return !props.goalId || goal ? (
        <>
            <Form
                form={form}
                name="dynamic_form_nest_item"
                onFinish={onFinish}
                autoComplete="off"
                initialValues={
                    goal || {
                        name: 'New achievement',
                        repeat: 'no',
                        days: ['0'],
                    }
                }
                {...layout}
                onValuesChange={(changedFields, allValues) =>
                    allValues.days &&
                    form.setFieldsValue({ days: allValues.days.sort() })
                }
            >
                <Form.Item
                    name="images"
                    label="Images"
                    valuePropName="images"
                    getValueFromEvent={normFile}
                >
                    <Upload
                        listType="picture-card"
                        fileList={images.map((image, index) => ({
                            ...image,
                            uid: index,
                        }))}
                        onPreview={file => {
                            setPreviewImage(file.url)
                        }}
                        onChange={({ fileList }) => setImages(fileList)}
                        customRequest={({ file }) => {
                            uploadFile(file, 800, undefined, url => {
                                setImages(
                                    [...images, { url }].map(
                                        (image, index) => ({
                                            ...image,
                                            uid: index,
                                        })
                                    )
                                )
                            })
                        }}
                    >
                        {images.length >= 8 ? null : (
                            <div
                                style={{
                                    flex: 1,
                                    display: 'flex',
                                    height: '100%',
                                    alignItems: 'center',
                                    justifyContent: 'center',
                                    flexDirection: 'column',
                                }}
                                onClick={e => {
                                    if (window.isRNWebView) {
                                        e.stopPropagation()
                                        window.ReactNativeWebView.postMessage(
                                            'uploadFile'
                                        )
                                        window.callback = url => {
                                            setImages(
                                                [...images, { url }].map(
                                                    (image, index) => ({
                                                        ...image,
                                                        uid: index,
                                                    })
                                                )
                                            )
                                        }
                                    }
                                }}
                            >
                                <PlusOutlined />
                                <div className="ant-upload-text">Upload</div>
                            </div>
                        )}
                    </Upload>
                </Form.Item>
                <Form.Item label="Name" name="name" rules={nameRules}>
                    <Input />
                </Form.Item>
                <Form.Item
                    label="Description"
                    name="description"
                    rules={descriptionRules}
                >
                    <Input.TextArea
                        autoSize={{ minRows: 4, maxRows: 20 }}
                        placeholder="Write a few words about why this goal is important and how to achieve it."
                    />
                </Form.Item>
                <Form.Item label="Achievers" name="users">
                    {connections}
                </Form.Item>

                <Form.Item label="Repeat" name="repeat">
                    <Select>
                        <Option value="no">No repeat</Option>
                        <Option value="day">Every day</Option>
                        <Option value="weekday">Every day of week</Option>
                        <Option value="week">Every week</Option>
                        <Option value="month">Every month</Option>
                        <Option value="year">Every year</Option>
                    </Select>
                </Form.Item>
                <Form.Item
                    noStyle
                    shouldUpdate={(prevValues, currentValues) =>
                        prevValues.repeat !== currentValues.repeat
                    }
                >
                    {({ getFieldValue }) => {
                        return getFieldValue('repeat') === 'weekday' ? (
                            <Form.Item label="Days of week" name="days">
                                <Select mode="tags">
                                    <Option value="0">Sunday</Option>
                                    <Option value="1">Monday</Option>
                                    <Option value="2">Tuesday</Option>
                                    <Option value="3">Wednesday</Option>
                                    <Option value="4">Thursday</Option>
                                    <Option value="5">Friday</Option>
                                    <Option value="6">Saturday</Option>
                                </Select>
                            </Form.Item>
                        ) : null
                    }}
                </Form.Item>
                <Rewards
                    label="Rewards"
                    rewards={rewards}
                    setRewards={setRewards}
                />
                <Form.Item label="Place">
                    <MapSearch
                        map={map}
                        setPosition={value => {
                            setPosition(value)
                            form.setFieldsValue({ nomap: false })
                        }}
                    />
                    <div
                        style={{
                            position: 'relative',
                            height: '300px',
                        }}
                    >
                        <Map
                            ref={map}
                            center={position}
                            zoom={zoom}
                            style={{
                                width: '100%',
                                height: '100%',
                                position: 'relative',
                                zIndex: '1',
                            }}
                            ondragend={mapDragEnd}
                            onzoomend={e => mapDragEnd(e, true)}
                            zoomAnimation={false}
                        >
                            <TileLayer
                                attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                            />
                            <LocateControl />
                        </Map>
                        <div
                            style={{
                                backgroundImage: `url(${require('../../assets/images/pin.png')})`,
                                width: '20px',
                                height: '35px',
                                position: 'absolute',
                                zIndex: 2,
                                left: 'calc(50% - 10px)',
                                top: 'calc(50% - 35px)',
                                pointerEvents: 'none',
                                touchAction: 'none',
                            }}
                        />
                    </div>
                </Form.Item>
                <Form.Item
                    label="No location"
                    name="nomap"
                    valuePropName="checked"
                >
                    <Checkbox />
                </Form.Item>
                <div style={{ textAlign: 'right', marginTop: '50px' }}>
                    <Button
                        type="text"
                        size="large"
                        onClick={() => props.cancel(false)}
                    >
                        Cancel
                    </Button>
                    <Button
                        type="primary"
                        htmlType="submit"
                        size="large"
                        style={{
                            borderRadius: '6px',
                            padding: '0px 25px',
                            marginLeft: '10px',
                        }}
                    >
                        Save
                    </Button>
                </div>
            </Form>

            <Modal
                visible={previewImage}
                footer={null}
                onCancel={() => {
                    setPreviewImage('')
                    document.body.removeAttribute('style')
                }}
                destroyOnClose={true}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={previewImage}
                />
            </Modal>
        </>
    ) : null
}

export default EditGoalAndProgressForm
