import React, { useEffect, useState, useRef, useCallback } from 'react'
import { Route, Switch, Redirect, withRouter } from 'react-router-dom'

import AuthTest from './containers/Auth/AuthTest'
import './components/UI/loadStyles'
import AccountLayout from './components/Layout/Layout'
import Progress from './containers/Progress/Progress'
import Dashboard from './containers/Dashboard/Dashboard'
import Blog from './containers/Blog/Blog'

import Profile from './containers/Profile/Profile'

import ActivateAccount from './containers/ActivateAccount/ActivateAccount'
import Cookies from 'universal-cookie'
import Perk from './containers/Shop/Perk'

import WishlistItem from './containers/Wishlist/WishlistItem'
import { connect } from 'react-redux'
import * as actions from './store/actions'
import ConnectWebsocket from './containers/ConnectWebsocket/ConnectWebsocket'
import NotificationsTracker from './containers/Dashboard/NotificationsTracker'
import Modal from 'antd/es/modal/Modal'
import Button from 'antd/es/button'
import RNBridge from './RNBridge'

const cookies = new Cookies()

const App = props => {
    const mountedRef = useRef(true)
    const [timer, setTimer] = useState(null)
    const [explanationVisible, setExplanationVisible] = useState(false)
    const [showSignIn, setShowSignIn] = useState(false)
    const { isAuthed } = props

    const auth = useCallback(async () => {
        try {
            if (!mountedRef.current) return null
            props.tryAuth()
        } catch (error) {}
    }, [mountedRef])
    useEffect(() => {
        if (!isAuthed) {
            auth()
        }
        return () => {
            mountedRef.current = false
        }
    }, [isAuthed])

    if (!isAuthed) {
        if (!cookies.get('explanationSeen') && !timer)
            setTimer(setTimeout(() => setExplanationVisible(true), 5000))
    } else if (timer) {
        window.clearTimeout(timer)
    }

    const Logout = () => {
        props.logout()
        window.isRNWebView && window.ReactNativeWebView.postMessage('logout')
        return <Redirect to="/explore" />
    }

    // useEffect(() => {
    //     if(window.isRNWebView){
    //         if(props.history)
    //     }
    //         // ? window.ReactNativeWebView.postMessage('showLogin')
    // })
    let routes = (
        <Switch>
            <Route path="/" exact component={Dashboard} />

            <Route path="/inspirations" component={Progress} />

            <Route path="/profile" component={Profile} />

            <Route path="/account" exact component={Profile} />
            <Route path="/shop" component={Perk} />
            <Route path="/wishlist" component={WishlistItem} />
            <Route path="/logout" exact component={Logout} />
            <Route path="/ru" component={Blog} />
            <Route path="/blog" component={Blog} />

            <Route path="/activate-account" exact component={ActivateAccount} />

            {process.env.NODE_ENV === 'development' && !isAuthed && (
                <Route path="/logintest" exact component={AuthTest} />
            )}
            <Redirect to="/" />
        </Switch>
    )

    if (props.accountNotActive) {
        routes = (
            <Switch>
                <Route path="/logout" exact component={Logout} />

                <Route
                    path="/activate-account"
                    exact
                    component={ActivateAccount}
                />

                {process.env.NODE_ENV === 'development' && !isAuthed && (
                    <Route path="/logintest" exact component={AuthTest} />
                )}
                {isAuthed ? (
                    <Redirect to="/activate-account" />
                ) : (
                    <Redirect to="/" />
                )}
            </Switch>
        )
    }
    const handleExplanationClose = () => {
        cookies.set('explanationSeen', 'true', {
            path: '/',
            maxAge: 86400,
        })
        setExplanationVisible(false)
        setShowSignIn(true)
        document.body.removeAttribute('style')
    }

    return (
        <>
            <div>
                <AccountLayout showSignIn={showSignIn}>{routes}</AccountLayout>
                {isAuthed && <ConnectWebsocket />}
                {isAuthed && <NotificationsTracker />}
                <RNBridge />
            </div>
            {!window.isRNWebView && (
                <Modal
                    visible={explanationVisible}
                    // footer={<Button onClick={handleExplanationClose}>Close</Button>}
                    footer={null}
                    onCancel={handleExplanationClose}
                >
                    <div>
                        <h1>Welcome to Addspire!</h1>
                        <p style={{ fontSize: '16px' }}>
                            This is a social network where people share and get
                            support for their ideas, pomises and resolutions.
                        </p>
                        <h3 style={{ fontSize: '16px', marginTop: '30px' }}>
                            Here is how Addspire works:
                        </h3>

                        <p style={{ fontSize: '16px', marginTop: '30px' }}>
                            1. You can create an inspiration for your idea
                        </p>
                        <img
                            src="/intro/1.png"
                            style={{
                                width: '100%',
                                border: '1px solid #ccc',
                            }}
                        />

                        <p style={{ fontSize: '16px', marginTop: '50px' }}>
                            Or you can inspire people you care.
                        </p>
                        <img
                            src="/intro/2.png"
                            style={{
                                width: '100%',
                                border: '1px solid #ccc',
                            }}
                        />

                        <p style={{ fontSize: '16px', marginTop: '50px' }}>
                            2. Start working on your goal, share updates and
                            make posts. Your friends and the Addspire community
                            will support you.
                        </p>
                        <img
                            src="/intro/3.png"
                            style={{
                                width: '100%',
                                border: '1px solid #ccc',
                            }}
                        />

                        <p style={{ fontSize: '16px', marginTop: '50px' }}>
                            3. Achieve your goal and start a new one!
                        </p>
                        <img
                            src="/intro/4.png"
                            style={{
                                width: '100%',
                                border: '1px solid #ccc',
                            }}
                        />
                        <p style={{ fontSize: '16px', marginTop: '50px' }}>
                            More than 60% of people make promises and
                            resolutions every year and more than 90% of them
                            fail.
                        </p>
                        <h3 style={{ marginTop: '50px' }}>
                            Make sure that you will achieve your goals!
                        </h3>
                        <div style={{ textAlign: 'center' }}>
                            <Button
                                type="primary"
                                style={{
                                    background: 'green',
                                    fontSize: '20px',
                                    padding: '10px 40px',
                                    fontWeight: 'bold',
                                    height: 'auto',
                                    margin: '30px auto',
                                }}
                                onClick={handleExplanationClose}
                            >
                                Sign Up
                            </Button>
                        </div>
                    </div>
                </Modal>
            )}
        </>
    )
}

const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        accountNotActive: state.accountNotActive,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        tryAuth: () => dispatch(actions.tryAuth()),
        logout: () => dispatch(actions.logout()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(App)
)
