import React, { useEffect, useState, useRef } from 'react'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import axios from 'axios'
import InfiniteScroll from 'react-infinite-scroller'
import message from 'antd/es/message'

import Input from 'antd/es/input'
import MasonryGrid from '../../components/MasonryGrid'
import MasonryCard from '../../components/MasonryCard'
import Helmet from 'react-helmet'
import Button from 'antd/es/button'
import Modal from 'antd/es/modal/Modal'
import { Map, TileLayer } from 'react-leaflet'
import 'leaflet/dist/leaflet.css'
import Checkbox from 'antd/es/checkbox'
import Badge from 'antd/es/badge'
import Slider from 'antd/es/slider'
import InputNumber from 'antd/es/input-number'
import Select from 'antd/es/select'
import * as ELG from 'esri-leaflet-geocoder'
import 'esri-leaflet-geocoder/dist/esri-leaflet-geocoder.css'
import LocateControl from './LocateControl'

const Explore = props => {
    const map = useRef(null)
    const [isSearching, setIsSearching] = useState(false)
    const [mapVisible, setMapVisible] = useState(false)
    const [radius, setRadius] = useState(false)
    const [foundProgresses, setFoundProgresses] = useState([])
    const [progresses, setProgresses] = useState([])
    const [noMore, setNoMore] = useState(false)
    const [noMorePopular, setNoMorePopular] = useState(false)

    const [search, setSearch] = useState({
        value: '',
        withMap: false,
        distance: 20,
        zoom: 7,
        position: [40.689078, -74.044526],
        units: 'mi',
    })

    useEffect(() => {
        if (map && map.current && map.current.leafletElement) {
            new ELG.Geosearch().addTo(map.current.leafletElement)
        }
    }, [map.current])

    const onSearch = (value, noNoMore) => {
        if (!noMore || noNoMore) {
            setNoMore(true)
            axios
                .post('/api/explore/search', {
                    value: value ? { ...search, value } : search,
                    skip: noNoMore ? 0 : foundProgresses.length,
                })
                .then(res => {
                    if (res.data.success) {
                        setNoMore(res.data.noMore)
                        setFoundProgresses(res.data.progresses)
                        if (res.data.progresses.length === 0)
                            message.warning('There are no progresses found.')
                        const newFriendData = {}
                        for (let friend of res.data.friends) {
                            newFriendData[friend._id] = friend
                        }
                    } else {
                        if (res.data.errorCode) {
                            message.error(
                                'Something is wrong with the data. Please try one more time with new data.'
                            )
                        }
                    }
                })
                .catch(err => {})
        }
    }
    const onGetPopular = () => {
        if (!noMorePopular) {
            setNoMorePopular(true)
            axios
                .post('/api/explore/popular', {
                    skip: progresses.length,
                })
                .then(res => {
                    if (res.data.success) {
                        setNoMorePopular(res.data.noMore)
                        setProgresses([...progresses, ...res.data.progresses])
                        const newFriendData = {}
                        for (let friend of res.data.friends) {
                            newFriendData[friend._id] = friend
                        }
                    } else {
                        if (res.data.errorCode) {
                            message.error(
                                'Something is wrong with the data. Please try one more time with new data.'
                            )
                        }
                    }
                })
                .catch(err => {})
        }
    }

    const mapDragEnd = e => {
        setSearch({
            ...search,
            zoom: e.target._zoom,
            position: [e.target.getCenter().lat, e.target.getCenter().lng],
            withMap: true,
        })
        // if (fromZoom && map.current && map.current.leafletElement)
        //     map.current.leafletElement.panTo(search.position, {
        //         animate: false,
        //     })
    }

    useEffect(() => {
        getRadius()
    }, [search])

    const getRadius = () => {
        if (map && map.current && map.current.leafletElement) {
            const metresPerPixel =
                (40075016.686 *
                    Math.abs(
                        Math.cos(
                            (map.current.leafletElement.getCenter().lat *
                                Math.PI) /
                                180
                        )
                    )) /
                Math.pow(2, map.current.leafletElement.getZoom() + 8)

            setRadius(
                search.units === 'mi'
                    ? (search.distance * 1609) / metresPerPixel
                    : (search.distance * 1000) / metresPerPixel
            )
        }
    }

    return (
        <div>
            <Helmet>
                <title>
                    Explore inspirations and ideas from around the World!
                </title>
                <meta
                    name="description"
                    content="Addspire is a social network where you can inspire and motivate yourself or the people you care."
                />
            </Helmet>
            <div
                style={{
                    maxWidth: '500px',
                }}
            >
                <Input.Search
                    placeholder="Find great inspirations!"
                    onSearch={value => {
                        if (value) {
                            setIsSearching(true)
                            if (value !== search.value) {
                                setNoMore(false)
                                setFoundProgresses([])
                                setSearch({ ...search, value })
                                onSearch(value, true)
                            }
                        } else {
                            setNoMore(false)
                            setSearch({ ...search, value })
                            setIsSearching(false)
                        }
                    }}
                    onChange={e => {
                        if (!e.target.value && !search.withMap) {
                            setNoMore(false)
                            setFoundProgresses([])
                            setIsSearching(false)
                            setSearch({ ...search, value: '' })
                        }
                    }}
                    enterButton
                    allowClear
                    style={{
                        borderRadius: '10px',
                    }}
                    suffix={
                        <Button
                            type="default"
                            size="small"
                            onClick={() => setMapVisible(true)}
                        >
                            <Badge color={search.withMap ? 'green' : 'orange'}>
                                Map
                            </Badge>
                        </Button>
                    }
                />

                <Modal
                    width="100%"
                    style={{ maxWidth: '1000px' }}
                    visible={mapVisible}
                    onCancel={() => setMapVisible(false)}
                    closable
                    footer={[
                        <Button
                            key="submit"
                            type="primary"
                            onClick={() => {
                                setMapVisible(false)
                                if (search.withMap || search.value) {
                                    setIsSearching(true)
                                    setNoMore(false)
                                    setFoundProgresses([])
                                    onSearch()
                                } else {
                                    onGetPopular()
                                }
                            }}
                        >
                            Ok
                        </Button>,
                    ]}
                >
                    Search with location:{' '}
                    <Checkbox
                        checked={search.withMap}
                        onClick={() => {
                            setSearch({ ...search, withMap: !search.withMap })
                        }}
                    />
                    <div style={{ display: 'flex' }}>
                        <span>Distance:</span>

                        <Slider
                            min={1}
                            max={500}
                            onChange={distance =>
                                setSearch({ ...search, distance })
                            }
                            value={search.distance}
                            style={{ flex: 1 }}
                        />

                        <InputNumber
                            size="small"
                            min={1}
                            max={500}
                            value={search.distance}
                            onChange={distance =>
                                setSearch({ ...search, distance })
                            }
                        />
                    </div>
                    <div>
                        <span>Units:</span>
                        <Select
                            value={search.units}
                            bordered={false}
                            onChange={units => setSearch({ ...search, units })}
                        >
                            <Select.Option value="mi">mi</Select.Option>
                            <Select.Option value="km">km</Select.Option>
                        </Select>
                    </div>
                    <div
                        style={{
                            position: 'relative',
                            height: '300px',
                            marginTop: '30px',
                            overflow: 'hidden',
                        }}
                    >
                        <Map
                            whenReady={() => {
                                getRadius()
                            }}
                            ref={map}
                            center={search.position}
                            zoom={search.zoom}
                            style={{
                                width: '100%',
                                height: '100%',
                                position: 'relative',
                                zIndex: 1,
                            }}
                            ondragend={mapDragEnd}
                            onmoveend={mapDragEnd}
                        >
                            <TileLayer
                                attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                            />
                            <LocateControl />
                        </Map>
                        <div
                            style={{
                                backgroundImage: `url(${require('../../assets/images/pin.png')})`,
                                width: '20px',
                                height: '35px',
                                position: 'absolute',
                                zIndex: 3,
                                left: 'calc(50% - 10px)',
                                top: 'calc(50% - 35px)',
                                pointerEvents: 'none',
                                touchAction: 'none',
                            }}
                        />
                        <div
                            style={{
                                background: 'rgba(82, 95, 165, .15)',
                                width: 2 * radius + 'px',
                                height: 2 * radius + 'px',
                                position: 'absolute',
                                zIndex: '2',
                                left: 'calc(50% - ' + radius + 'px)',
                                top: 'calc(50% - ' + radius + 'px)',
                                pointerEvents: 'none',
                                touchAction: 'none',
                                borderRadius: radius + 'px',
                                border: '1px rgba(82, 95, 165, .5) solid',
                            }}
                        />
                    </div>
                </Modal>
            </div>
            {isSearching ? (
                foundProgresses.length > 0 ? (
                    <InfiniteScroll
                        pageStart={0}
                        loadMore={onSearch()}
                        hasMore={!noMore}
                        loader={
                            <div className="loader" key={0}>
                                Loading ...
                            </div>
                        }
                    >
                        <MasonryGrid>
                            {foundProgresses.map((item, index) => (
                                <MasonryCard
                                    id={item._id}
                                    key={index}
                                    onClickLink={'/inspirations/' + item._id}
                                    images={item.goal.images}
                                    body={
                                        <>
                                            {/* <UserButton
                                                noName={true}
                                                id={item.owner}
                                                friendDataExp={friendData}
                                            /> */}
                                            <span
                                                style={{ fontWeight: 'bold' }}
                                            >
                                                {item.name}
                                            </span>
                                        </>
                                    }
                                />
                            ))}
                        </MasonryGrid>
                    </InfiniteScroll>
                ) : null
            ) : (
                <InfiniteScroll
                    pageStart={0}
                    loadMore={onGetPopular}
                    hasMore={!noMorePopular}
                    loader={
                        <div className="loader" key={0}>
                            Loading ...
                        </div>
                    }
                >
                    <MasonryGrid>
                        {progresses.map((item, index) => (
                            <MasonryCard
                                id={item._id}
                                key={index}
                                onClickLink={'/inspirations/' + item._id}
                                images={item.goal.images}
                                body={
                                    <>
                                        {/* <UserButton
                                            noName={true}
                                            id={item.owner}
                                            friendDataExp={friendData}
                                        /> */}
                                        <span style={{ fontWeight: 'bold' }}>
                                            {item.name}
                                        </span>
                                    </>
                                }
                            />
                        ))}
                    </MasonryGrid>
                </InfiniteScroll>
            )}
        </div>
    )
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
    }
}

export default connect(
    null,
    mapDispatchToProps
)(Explore)
